import classHelper from './helper/class-helper.js';
import DaoDialog from './dao-dialog/index.js';
import './dao-button/dao-button.scss';
import DaoTooltip from './dao-tooltip/index.js';
import DaoPopover from './dao-popover/index.js';
var app     = angular.module('daoStyle', [
  DaoDialog,
  DaoTooltip,
  DaoPopover,
]);

var appHelp = classHelper('daoStyle');

import DaoInputDirective from './dao-input/dao-input.directive.js';
import DaoDemoBlockDirective from './dao-demo/dao-demo.directive.js';

app
  .directive('daoInput', DaoInputDirective);

appHelp
  .directive('daoDemoBlock', DaoDemoBlockDirective);

export default 'daoStyle';



